<template>
<div>
    <el-table
    :data="tableData" 
    style="width: 100%">
    <el-table-column
      label=""
      width="180">
      <template slot-scope="scope1">
        <i class="el-icon-time"></i>
        <span style="margin-left: 10px">{{ scope1.row.date }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      width="180">
      <template slot-scope="scope">
        <el-popover trigger="hover" placement="top">
          <p>: {{ scope.row.name }}</p>
          <p>: {{ scope.row.address }}</p>
          <div slot="reference" class="name-wrapper">
            <el-tag size="medium">{{ scope.row.name }}</el-tag>
          </div>
        </el-popover>
      </template>
    </el-table-column>

    <el-table-column
        label=""
        width="300">
        <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.address }}</span>
        </template>
    </el-table-column>

    <el-table-column label="">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)"></el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)"></el-button>
      </template>
    </el-table-column>
  </el-table>

<el-dialog
  title=""
  :visible.sync="centerDialogVisible"
  width="30%"
  center>
  <el-dialog
      width="80%"
      :visible.sync="innerVisible"
      append-to-body>
      <pickUpMap v-on:getLngLat="getzuobiao" ></pickUpMap>
    </el-dialog>
    <el-form  :model="temp" label-position="left" label-width="70px" style='width: 400px; margin-left:50px;'>
        
        <el-form-item label="" prop="date" >
          <el-date-picker v-model="temp.date" type="datetime" placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" >
          <el-input v-model="temp.name"></el-input>
        </el-form-item>
        <el-form-item label="" >
          <el-input v-model="temp.address"></el-input>
        </el-form-item>
        <el-form-item>
      <el-button type="primary" @click="innerVisible = true"></el-button>
      <el-input v-model="temp.x" :disabled="true"/>
      <el-input v-model="temp.y" :disabled="true"/>
       </el-form-item>
      </el-form>
      
</el-input>
  <span slot="footer" class="dialog-footer">
    <el-button @click="centerDialogVisible = false"> </el-button>
    <el-button type="primary" @click="centerDialogVisible = false"> </el-button>
  </span>
</el-dialog>

</div>
  
</template>

<script>
import draw from "@/views/map/leaflet/draw";
import pickUpMap from "@/components/Map/PickUpMap";
export default {
  components: {
    draw,
    pickUpMap
  },
  data() {
    return {
      centerDialogVisible: false,
      innerVisible: false,
      tableData: [
        {
          date: "2016-05-02",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-04",
          name: "",
          address: " 1517 "
        },
        {
          date: "2016-05-01",
          name: "",
          address: " 1519 "
        },
        {
          date: "2016-05-03",
          name: "",
          address: " 1516 "
        }
      ],
      temp: {
        date: new Date(),
        name: "",
        address: "",
        x:null,
        y:null
      }
    };
  },
  methods: {
    //
    handleEdit(index, row) {
      // console.log(index, row);
      this.temp = Object.assign({}, row); // copy obj
      this.temp.date = new Date(this.temp.date);
      this.centerDialogVisible = true;
    },
    // 
    handleDelete(index, row) {
      console.log(index, row);
    },
    //map
    getzuobiao(lng, lat) {
      this.temp.x=lng;
      this.temp.y=lat
      console.log(lng + "adfas" + lat);
    }
  }
};
</script>


